.app--theme_default.app,
.app--theme_default .lusamine-alert,
.app--theme_default .app-information__detail {
  background-color: #f9f6c4;
  color: #000; }

.app--theme_default .team-breakdown__item,
.app--theme_default .saved-team {
  border-color: #567647; }

.app--theme_default .breakdown-item-title__text {
  background-color: #f9f6c4; }

.app--theme_default .header-bar {
  background-color: #f9f6c4;
  color: #000; }
  .app--theme_default .header-bar a:hover, .app--theme_default .header-bar a.active {
    background-color: #e3d974;
    color: #48633c; }

.app--theme_default .lusamine-button {
  background-color: #f9f6c4; }
  .app--theme_default .lusamine-button:hover {
    background-color: #dacd4a;
    color: #567647; }

.app--theme_default .lusamine-button-link,
.app--theme_default .app-information {
  color: #567647; }
  .app--theme_default .lusamine-button-link:hover,
  .app--theme_default .app-information:hover {
    color: #3f5634; }

.app--theme_default .lusamine-button--action {
  border: 1px solid #e3d974; }

.app--theme_default .lusamine-multiselect-list {
  background-color: #f9f6c4; }

.app--theme_default .team-member__action:focus {
  box-shadow: 0px 0px 2px 3px #567647; }

.app--theme_default .easter-egg {
  background-color: #e3d974; }
  .app--theme_default .easter-egg__label {
    color: #567647; }

@font-face {
  font-family: 'Roboto Mono';
  font-display: fallback;
  font-style: normal;
  font-weight: 400;
  src: local("Roboto Mono"), local("RobotoMono-Regular"), url("./roboto-mono.woff2") format("woff2");
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD; }

body {
  font-size: 16px; }

main {
  min-height: calc(100vh - 5px - 50px);
  padding: 5px;
    padding-top: 50px; }

.flex-spacer {
  flex: 1 1; }

.padding-5 {
  padding: 5px; }

.margin-one {
  margin: 1px; }

.list-item-bottom-spacing {
  margin-bottom: 4px; }

.settings__input {
  white-space: pre-line; }
